<template>
	<view>
		<input id="phone_input" @blur="Written1" placeholder="请输入可用的手机号:">
		<input id="password" @blur="Written2" placeholder="密码">
		<input id="confirm-input" @blur="Written3" placeholder="输入你收到的短信验证码">
		<button @click="Clicked">获取短信验证码</button>
		<button @click="Rigister">注册</button>
	</view>
</template>

<script>
	export default {
		data() {
			return {
				phone_number:'',
				password:'',
				confirm:'',
				confirm_true:''
			}
		},
		methods: {
			Written1:function(event){
				var value=event.target.value;
				console.log(value)
				this.phone_number=value
			},
			Written2:function(event){
				var value=event.target.value;
				console.log(value);
				this.password=value
			},
			Written3:function(event){
				var value=event.target.value;
				console.log(value)
				this.confirm=value
			},
			Clicked:function(event){
				console.log(this.phone_number)
				uni.request({
					url:'http://localhost:5000/register/confirm',
					
					data:{
						phone_number:this.phone_number
					},
					header:{
						//'content-type': 'application/x-www-form-urlencoded',
						'Access-Control-Allow-Origin':'*',
						'Cache_Control':'no-cache',
						'Pragma':'no-cache',
						'Expires':0
					},
					method:'POST',
					success:(res)=>{
						console.log(res.data);
						this.confirm_true=res.data.confirm;
					}
					
				})
				
			},
			Rigister:function(event){
				if(this.confirm_true==this.confirm){
					uni.request({
						url:'http://localhost:5000/register/register',
						method:'POST',
						data:{
							phone_number:this.phone_number,
							password:this.password
						},
						
						
						success:(res)=>{
							console.log(res.data.phone_number)
							console.log(res.data.password)
							if(res.statusCode==200){
								uni.redirectTo({
									url:'/pages/register/load/load'
								})
							}
						}
					})
				}
				
				
			}
			
		}
	}
</script>

